<template>
  <div style="padding-top: 44px;" class="goods goods-detail" @click="visibleService=false">
    <back-header title="商品详情">
      <van-icon class="shopping-cart" @click="$router.push('/shopping')" name="shopping-cart-o" />
    </back-header>

    <scroll ref="scroll" clickable>
      <van-swipe loop width="100%" @change="onChange">
        <van-swipe-item v-for="(item, index) in covers" :key="index">
          <div class="goods-banner-img" :style="{backgroundImage: `url(${item})`}"></div>
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">
            {{ current + 1 }}/{{ covers.length }}
          </div>
        </template>
      </van-swipe>

      <div class="goods-desc">
        <p class="price">￥{{ goods.price }}</p>
        <p class="desc van-multi-ellipsis--l2">{{ goods.title }}</p>
        <div class="nums">
          <span>运费：{{ goods.freight }}元</span>
          <span>已售{{ goods.sale_count }}件</span>
          <span>{{ goods.address }}</span>
        </div>
        <div class="service">
          <span>发货</span>
          <p>下单后{{ goods.delivery }}小时内发货</p>
        </div>
        <div class="service" @click.stop="visibleService=true">
          <span>服务</span>
          <p><van-icon class="verify-icon" name="passed" />已交保证金</p>
          <p><van-icon class="verify-icon" name="passed" />店铺资质认证</p>
          <div style="flex: 1;"></div>
          <van-icon class="icon-right" name="arrow" />
        </div>
      </div>

      <router-link :to="`/goods/${goods.id}/comment`" class="comment" v-if="evaluateCount">
        <div class="title">
          <h4>商品评价({{ evaluateCount }})</h4>
          <span>查看全部 &nbsp;&nbsp;<van-icon class="icon-right" name="arrow" /></span>
        </div>
        <div class="single-comment">
          <div class="user">
            <img :src="evaluate.user.avatar || $store.state.defaultAvatar" alt="">
            <p>{{ evaluate.user.nick_name }}</p>
          </div>
          <p class="van-multi-ellipsis--l2">{{ evaluate.content }}</p>
        </div>
      </router-link>

      <router-link class="store" :to="'/stores/' + goods.shopid">
        <img :src="goods.shop.user.avatar || $store.state.defaultAvatar" alt="">
        <p>{{ goods.shop.user.nick_name }}</p>
        <div style="flex: 1"></div>
        <button>进店逛逛</button>
      </router-link>

      <div class="detail">
        <h5>商品详情</h5>
        <p>{{ goods.desc }}</p>
        <img @load="refresh" v-for="(item, index) in detailImgs" :key="index" :src="item" alt="">
      </div>
    </scroll>

    <div class="buy-control" ref="buyControl">
      <div class="link" @click="$router.push('/stores/1')">
        <img src="./shop@3x.png" alt="">
        <p>店铺</p>
      </div>
      <div class="link" @click="chat">
        <img src="./kefu@3x.png" alt="">
        <p>客服</p>
      </div>
      <div style="flex: 1"></div>
      <div class="btns">
        <div class="btn" @click="slideSize(0)">加入购物车</div>
        <div class="btn" @click="slideSize(1)">立即购买</div>
      </div>
    </div>

    <transition name="slide">
      <service-alert @close="visibleService=false" v-show="visibleService" />
    </transition>

    <transition name="slide">
      <size-select
        :sizes="sizes"
        :colors="colors"
        :rules="rules"
        :default-img="covers[0]"
        @confirm="confirm"
        @close="visibleSizeSelect=false"
        v-if="visibleSizeSelect" />
    </transition>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import ServiceAlert from '../../components/service-alert/service-alert'
import Scroll from '../../components/scroll/scroll'
import SizeSelect from '../../components/size-select/size-select'
import { post } from '../../common/js/http'

export default {
  name: 'goods',
  components: {
    BackHeader,
    ServiceAlert,
    Scroll,
    SizeSelect
  },
  data() {
    return {
      current: 0,
      covers: [],
      visibleService: false,
      visibleSizeSelect: false,
      confirmType: 0,
      detailImgs: [],
      goods: {
        shop: {
          user: {}
        }
      },
      colors: [],
      sizes: [],
      rules: [],
      evaluateCount: 0,
      evaluate: {}
    }
  },
  mounted() {
    this.getDetail()
  },
  activated() {
    this.getDetail()
  },
  methods: {
    chat() {
      this.$jsCallNative('chat', {
        uid: this.goods.shop.id + '',
        nick_name: this.goods.shop.user.nick_name
      })
    },
    onChange(index) {
      this.current = index
    },
    refresh() {
      this.$nextTick(() => {
        this.$refs.scroll && this.$refs.scroll.refresh()
      })
    },
    slideSize(type) {
      this.confirmType = type
      this.visibleSizeSelect = true
    },
    confirm(data) {
      this.visibleSizeSelect = false
      if (this.confirmType === 1) {
        this.$router.push({
          name: 'place-order',
          params: {
            order: [
              {
                shopid: this.goods.shop.id,
                store: this.goods.shop.user,
                remark: '',
                goods: [
                  {
                    ...this.goods,
                    ...data
                  }
                ]
              }
            ]
          }
        })
      } else {
        const loading = this.$toast.loading()
        post('/shop/cart/addCartGoods', {
          inventoryid: data.inventoryid,
          count: data.count,
          shopid: this.goods.shopid
        })
          .then(() => {
            loading.close()
          })
      }
    },
    getDetail() {
      const loading = this.$skeLoading('.goods-detail')
      post('/shop/goods/getGoodsInfo', {
        goodsid: this.$route.params.id
      })
        .then(({ data: { data } }) => {
          this.covers = data.goods.thumb_urls.split(',')
          this.detailImgs = data.goods.desc_img_urls.split(',')
          this.goods = data.goods
          this.colors = data.colors
          this.sizes = data.sizes
          this.rules = data.inventory
          this.evaluateCount = data.evaluate_count
          this.evaluate = data.evaluate
          loading.close()
        })
    }
  }
}
</script>

