<template>
  <div class="size-select">
    <van-icon name="cross" @click="$emit('close')" class="close" />
    <div class="info">
      <div class="cover" :style="{backgroundImage: `url(${activeColor.img_url || defaultImg})`}"></div>
      <div class="price">
        <h4>￥{{ totalPrice }}</h4>
        <p>库存{{ activeRule.left_count }}件</p>
        <span>请选择 颜色分类 尺码</span>
      </div>
    </div>
    <div class="select">
      <h5>颜色分类</h5>
      <ul>
        <li @click="activeColor=item" v-for="item in colors" :key="item.id" :class="{active: item.id === activeColor.id}">{{ item.color }}</li>
      </ul>
    </div>
    <div class="select" v-if="sizes.length">
      <h5>尺码</h5>
      <ul>
        <li @click="activeSize=item" :class="{active: activeSize.id === item.id}" v-for="item in sizes" :key="item.id">{{ item.size }}</li>
      </ul>
    </div>
    <div class="num">
      <label>数量</label>
      <van-stepper :max="activeRule.left_count" button-size="0.66rem" input-width="0.66rem" disable-input v-model="num" />
    </div>
    <button @click="confirm">确定</button>
  </div>
</template>

<script>
export default {
  name: 'size-select',
  props: {
    defaultImg: {
      type: String,
      default: ''
    },
    colors: {
      type: Array,
      default() {
        return []
      }
    },
    sizes: {
      type: Array,
      default() {
        return []
      }
    },
    rules: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      num: 1,
      activeColor: 0,
      activeSize: 0,
      cover: ''
    }
  },
  mounted() {
    this.activeColor = this.colors.length && this.colors[0]
    this.cover = this.colors.length && this.colors[0].img_url
    this.activeSize = this.sizes.length ? this.sizes[0] : { id: 0 }
  },
  computed: {
    activeRule() {
      let rule = {}
      this.rules.some(item => {
        if (item.colorid === this.activeColor.id && item.sizeid === this.activeSize.id) {
          rule = item
          return true
        }
      })
      return rule
    },
    totalPrice() {
      return (this.activeRule.price * this.num).toFixed(2)
    }
  },
  methods: {
    confirm() {
      this.$emit('confirm', {
        count: this.num,
        inventoryid: this.activeRule.id,
        size: this.activeColor.color + this.activeSize.size,
        price: this.activeRule.price,
        cover: this.activeColor.img_url || this.defaultImg
      })
    }
  }
}
</script>

